	  whatPieceIsMoved(piece,oldMove,nextMove ){
		var piece_moved;
		console.log(piece);
		console.log(oldMove);
		console.log(nextMove);
		var piece_moved_arr = piece.split(",");
		console.log(piece_moved_arr);
		for(var i=0;i < piece_moved_arr.length;i++){
		  //console.log((piece_moved_arr[i].substring(1,3).toUpperCase());
		  if(piece_moved_arr[i].substring(1,3).toUpperCase() == oldMove.toUpperCase()) {
			piece_moved = piece_moved_arr[i].substring(8, 9).toUpperCase();
			for(var j=0;j < piece_moved_arr.length;j++) {
			  if (piece_moved_arr[j].substring(1, 3).toUpperCase() ==
				  nextMove.toUpperCase()) {
				piece_moved = piece_moved_arr[i].substring(8, 9).toUpperCase() + 'x';
			  }
			}
		  }
		}
		console.log(piece_moved);
		return piece_moved;
	}

	CheckExistPiece(pieces,newMove){
		var pieces_arr = pieces.replaceAll("\"","").split(",");
		console.log(pieces_arr);
		console.log(newMove);
		for(let f=0;f<pieces_arr.length;f++){
			if(pieces_arr[f].split(":")[0].toLowerCase() == newMove){
				return true;
			}
		}
		return false;
	}

	flipMove(moved){
		console.log(moved.slice(-1));
		if(parseInt(moved.slice(-1))===1){
			moved = moved.slice(0, -1) + '8';
		}else if(parseInt(moved.slice(-1))===2){
			moved = moved.slice(0, -1) + '7';
		}else if(parseInt(moved.slice(-1))===3){
			moved = moved.slice(0, -1) + '6';
		}else if(parseInt(moved.slice(-1))===4){
			moved = moved.slice(0, -1) + '5';
		}else if(parseInt(moved.slice(-1))===5){
			moved = moved.slice(0, -1) + '4';
		}else if(parseInt(moved.slice(-1))===6){
			moved = moved.slice(0, -1) + '3';
		}else if(parseInt(moved.slice(-1))===7){
			moved = moved.slice(0, -1) + '2';
		}else if(parseInt(moved.slice(-1))===8){
			moved = moved.slice(0, -1) + '1';
		}
		console.log(moved);
		return moved;
	}

	getRandomMove(fen) {
		console.log(fen);
		var chess = new Game(fen);
	  
		var moves = chess.moves();
		
	  	//console.log(moves);
		//console.log(Math.floor(Math.random() * Object.getOwnPropertyNames(moves).length));
		//console.log(moves[Math.floor(Math.random() * Object.getOwnPropertyNames(moves).length)]);
		//console.log(Object.values(moves)[Math.floor(Math.random() * Object.getOwnPropertyNames(moves).length)][1].toLowerCase());
		if(chess.in_checkmate===true){
		  return 'chessmat';
		}else if(chess.in_draw===true){
		  return 'chessdraw';
		}else if (moves.isEmpty===true) {
		  return 'mat';
		}
		var randomMove = Math.floor(Math.random() * Object.getOwnPropertyNames(moves).length);
		var newMoveFrom = Object.keys(moves)[randomMove];
		//console.log("newMoveFrom-"+newMoveFrom);

		var newMoveToIndex = Math.floor(Math.random() * Object.values(moves)[randomMove].length);
		var newMoveTo = Object.values(moves)[randomMove][newMoveToIndex];
		//console.log("newMoveTo-"+newMoveTo);

		//this.chessgame.move(newMoveFrom,newMoveTo);
	  
		return newMoveFrom.toLowerCase()+","+newMoveTo.toLowerCase();
	}

	checkIsValidMove(from,to,fen){
		var chess = new Game(fen);
	  
		var moves = chess.moves(from);
		console.log(moves);
		for(let i=0;i<moves.length;i++){
			if(moves[i].toUpperCase() == to.toUpperCase()){
				return true;
			}
		}
		return false;
	}
  
