import React,{ useRef,useState,useEffect } from 'react';

import $ from 'jquery';

const dashboard = function Dashboard() {
  const bgstyle={
    backgroundColor: (localStorage.getItem('backgroundColor')=== null)?"#282c34":localStorage.getItem("backgroundColor"),
    color: 'white'
  }

  useEffect(() => {
    $(".container-fluid").css("position","fixed");
    $('.container').css("background-color",localStorage.getItem("backgroundColor")?localStorage.getItem("backgroundColor"):'#282c34');
  })

  return (
      <div className="container" style={bgstyle}>
        <h2>This is Dashboard Page</h2>
        
      </div>
    );
}

export default dashboard;